import React, { memo } from 'react';
import styled from 'styled-components';
import { colors, viewports, fontFamilies } from '../../utils/variables';
import { BodyText } from '../styled/Typography';
import Button from '../styled/Button';
import { useMedia } from 'the-platform';

const PopUp = ({
  index,
  title,
  setTourScreenIndex,
  setActiveScreen,
  copy,
  position,
  linePosition,
}) => {
  const medium = useMedia(viewports.medium);

  return (
    <Base style={medium ? position : {}}>
      {medium && <Line style={linePosition} />}
      <Container>
        <Header>
          <Index>{index + 1}/4</Index>
          <SkipScreen onClick={() => setActiveScreen(undefined)}>
            Skip Tour
          </SkipScreen>
        </Header>
        <Title>{title}</Title>
        <BodyText>{copy}</BodyText>
        <Pagination centered={index === 0 || index === 3}>
          {index > 0 && index < 3 && (
            <NavButton secondary onClick={() => setTourScreenIndex(index - 1)}>
              Back
            </NavButton>
          )}
          <NavButton
            onClick={() =>
              index !== 3
                ? setTourScreenIndex(index + 1)
                : setActiveScreen(undefined)
            }
          >
            {index === 3 ? 'Got it.' : 'Next'}
          </NavButton>
        </Pagination>
      </Container>
    </Base>
  );
};

export default memo(PopUp);

const Base = styled.div`
  position: fixed;
`;

const Line = styled.div`
  background: #fff;
  height: 2px;
  position: absolute;
  width: 1.875rem;
`;

const Container = styled.div`
  background: #fff;
  padding: 1.5rem 1.5rem 2.125rem;
  width: 16.25rem;
`;

const Header = styled.div`
  display: flex;
  justify-content: space-between;
  margin-bottom: 1.625rem;
`;

const Index = styled.div`
  color: #999999;
  font-size: 0.75rem;
`;

const SkipScreen = styled.span`
  color: ${colors.blue};
  cursor: pointer;
  display: none;
  font-size: 0.75rem;

  @media ${viewports.medium} {
    display: block;
  }
`;

const Title = styled.h3`
  color: ${colors.black};
  font-family: ${fontFamilies.bold};
  font-size: 1.375rem;
  margin-bottom: 0.875rem;
`;

const Pagination = styled.div`
  display: flex;
  justify-content: ${props => (props.centered ? 'center' : 'space-between')};
`;

const NavButton = styled(Button)`
  height: 1.875rem;
  font-size: 0.75rem;
  margin: 0;
  width: 4.375rem;
`;
